










































.vue-toast {
  left: 20%;
  width: 60%;
  z-index: 10000;
  position: fixed;
  padding: 20px 5px;
  text-align: center;
  font-size: 24px;
  &-bottom {
    bottom: 10%;
  }
  &-top {
    top: 10%;
  }
  &-middle {
    top: 50%;
    margin-top: -22px;
  }
  &--default {
    color: #fff;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.8);
  }
}
.vue-toast-fade-bottom {
  &-enter-active,
  &-leave-active {
    transition: opacity 0.5s, transform 0.3s;
  }
  &-enter,
  &-leave-to {
    opacity: 0;
    transform: translateY(100%);
  }
}
.vue-toast-fade-top {
  &-enter-active,
  &-leave-active {
    transition: opacity 0.5s, transform 0.3s;
  }
  &-enter,
  &-leave-to {
    opacity: 0;
    transform: translateY(-100%);
  }
}
